  &:hover
    box-shadow 1px 1px 0 #454b4d inset, -1px -1px 0 #454b4d inset
    color #454b4d

.btn-accent
  background #f97d38
  color #fff
  &:hover
    background #fff
    color #f97d38

.btn-white
  background #fff
  color #2f3334
  &:hover
    background #2f3334
    color #fff

.btn-outline-accent
  background #fff
  color #2f3334
  &:hover
    background #f97d38
    color #fff

.bg-white
  background #fff
  color #000

.bg-dark
  background #2f3334
  color #fff

